<template>
<div class="article">
  <div class="loading" v-if="isLoading">
    <img src="../assets/loading.gif" >
  </div>
  <div v-else>

    <div class="topic_header">
      <div class="topic_title">{{post.title}}</div>
      <!-- 标题之外的 header里面的信息! -->
      <ul>
        <li>•发布于:{{post.create_at | formatDate}}</li>
        <li>•作者:{{post.author.loginname}}</li>
        <li>•浏览次数:{{post.visit_count}}</li>
        <li>•来自{{post|tabFormatter}}</li>
      </ul>
      <div v-html="post.content" class="topic_content"></div>
    </div>

    <div id="reply">
      <div class="topbar">{{post.reply_count}} 回复</div>
      <div v-for="(reply,index)  in post.replies" class="replySec" :key="index" >
          <div class="replyUp">
            <router-link :to="{
            name:'user_info'
            ,params:{
              name:reply.author.loginname
            }}">
            <img :src="reply.author.avatar_url">
            </router-link>
            <router-link :to="{
            name:'user_info'
            ,params:{
              name:reply.author.loginname
            }}">
            <!-- 评论作者的登录名! -->
            <span>{{reply.author.loginname}}</span>
            </router-link>
            <span>
              {{index+1}}楼!
            </span>
            <span v-if="reply.ups.length>0">
               ☝{{reply.ups.length}}
            </span>
            <span v-else></span>

          </div>
          <!-- 评论内容! -->
          <p v-html="reply.content"></p>
      </div>
    </div>

  </div>
</div>
</template>
<script>
export default {
  name:'Article',
  data:function(){
    return {
      isLoading:false,
      post:{}
    }
  },methods:{
    getArticleData:function(){
      this.$axios.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
      .then((response)=>{
        if (response.data.success) {
        this.isLoading=false;
        this.post=response.data.data;
        }
      }).catch((error)=>{
        console.log(error);
      })
    }
  },beforeMount:function(){
      this.isLoading=true;
      this.getArticleData();
  },watch:{
    // 响应路由参数的变化! about vue-router

    '$route'(to,from){
      this.getArticleData();
    }
  }
}
</script>
<style>
/*
因为在页面中的内容是MarkDown形式的 因此需要引入MarkDown相关的CSS文件
并且style标签中在引入外部文件的时候 不能够有scoped属性!
*/
@import url('../assets/markdown-github.css');
  .topbar {
    padding: 10px;
    background-color: #f6f6f6;
    height: 16px;
    font-size: 12px;
    margin-top: 10px;
  }

  .article:not(:first-child) {
    margin-right: 340px;
    margin-top: 15px;
  }

  #reply, .topic_header {
    background-color: #fff;
  }

  #reply {
    margin-top: 15px;
  }

    #reply img {
    width: 30px;
    height: 30px;
    position: relative;
    bottom: -9px;
  }

  #reply a, #reply span {
    font-size: 13px;
    color: #666;
    text-decoration: none;
  }
  .replySec{
    border-bottom:1px solid #e5e5e5;
    padding:0 10px;
  }

  .loading {
    text-align: center;
    padding-top: 300px;
  }

  .replyUp a:nth-of-type(2) {
    margin-left: 0px;
    display: inline-block;
  }

  .topic_header {
    padding: 10px;
  }

  .topic_title {
    font-size: 20px;
    font-weight: bold;
    padding-top: 8px;
  }

  .topic_header ul {
    list-style: none;
    padding: 0px 0px;
    margin: 6px 0px;
  }

  .topic_header li {
    display: inline-block;
    font-size: 12px;
    color: #838383;
  }

  .topic_content {
    border-top: 1px solid #e5e5e5;
    padding: 0 10px;
  }

  .markdown-text img {
    width: 92% !important;
  }
</style>




